/* App.css */

button {
  all: unset;
}

h1 {
  margin-block: 1rem !important;
}

.App {
  text-align: center;
  font-family: Arial, sans-serif;
  background-color: var(--background);
  color: var(--foreground);
  min-height: 100vh;
}

.App-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 1rem;
  background-color: var(--purple);
  color: var(--foreground);
}

.add-client-button {
  background-color: var(--purple);
  border: none;
  color: var(--foreground);
  padding: 0.5rem 1rem;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 1rem;
  margin: 0.5rem;
  cursor: pointer;
  border-radius: 50%;
  width: 2rem;
  height: 2rem;
  line-height: 1rem;
}

.tabs {
  display: flex;
  justify-content: center;
  margin: 1rem 0;
}

.tab {
  padding: 0.5rem 1rem;
  cursor: pointer;
  border: none;
  background: none;
  font-size: 1rem;
  color: var(--foreground);
}

.tab.active {
  border-bottom: 2px solid var(--purple);
}

.search-bar {
  padding: 0.5rem;
  width: 80%;
  max-width: 500px;
  margin: 0.5rem 0;
  border: 1px solid var(--border);
  border-radius: 5px;
  background-color: var(--input-bg);
  color: var(--foreground);
}

.client-list {
  list-style-type: none;
  padding: 0;
  top: 197.88px;
  overflow-y: auto;
  border: 1px solid var(--border);
  border-radius: 5px;
  margin: 0px;
}

.client-item {
  display: flex;
  align-items: center;
  padding: 1rem;
  border-bottom: 1px solid var(--border);
}

.client-link {
  text-decoration: none;
  color: inherit;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
}

.client-link:hover {
  background-color: var(--hover-bg);
}

.client-initials {
  width: 3rem;
  height: 3rem;
  background-color: var(--purple);
  color: var(--foreground);
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  margin-right: 1rem;
  min-width: 3rem;
}

.client-info-container {
  display: flex;
}

.client-info {
  text-align: left;
}

.client-name {
  font-size: 1.2rem;
}

.client-phone,
.client-email {
  color: #aaa;
}

.button-clicked {
  background-color: #f0f0f0;
  transition: background-color 0.3s ease;
}

.search-and-sort {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 10px;
  gap: 10px;
}

.dropdown-button {
  background-color: var(--purple) !important;
  border-color: var(--purple) !important;
  border-radius: 5px;
}

.dropdown-item.selected {
  color: var(--purple);
  text-decoration: underline;
}

.search-bar {
  flex: 1;
}

.swiper-button-prev:after,
.swiper-button-next:after {
  display: none !important;
}

.swiper-button-prev,
.swiper-button-next {
  display: none !important;
}

.swiper-pagination-bullet-active {
  background: white !important;
}

.swiper-pagination-bullet {
  background: white !important;
}

.swiper {
  width: 100% !important;
  height: 100% !important;
}

.disclaimer-popup {
  position: fixed;
  overflow: hidden; /* Avoid auto scroll on the main container */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2000;
  padding: 20px;
}

.disclaimer-popup-content {
  background-color: rgba(153, 45, 45, 0.1);
  padding: 20px;
  width: 90vw;
  max-width: 800px;
  max-height: 80vh;
  overflow-y: auto;
}
.disclaimer-popup-content * {
  max-width: 100%;
}

.disclaimer-popup p,
.disclaimer-popup ul {
  margin-bottom: 20px;
}

.disclaimer-popup ul {
  list-style-type: disc;
  padding-left: 1.5rem;
  margin-left: 0;
}

.disclaimer-popup li {
  margin-bottom: 10px;
  padding-left: 0;
  line-height: 1.5;
  width: max-content;
}

.disclaimer-popup button {
  margin-top: 20px;
  padding: 10px 20px;
  background-color: #ffffff;
  color: #000000;
  border: none;
  cursor: pointer;
  font-size: 16px;
}
